.iziModal {
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    background: #fff;
    box-shadow: 0 0 8px rgba(0, 0, 0, .3);
    transition: margin-top .3s ease, height .3s ease
}

.iziModal * {
    -webkit-font-smoothing: antialiased
}

.iziModal:after {
    content: '';
    width: 100%;
    height: 0;
    opacity: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 1;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0, transparent), color-stop(100%, rgba(0, 0, 0, .35)));
    background: -webkit-linear-gradient(top, transparent, rgba(0, 0, 0, .35));
    background: linear-gradient(180deg, transparent 0, rgba(0, 0, 0, .35));
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#59000000', GradientType=0);
    transition: height .3s ease-in-out, opacity .3s ease-in-out;
    pointer-events: none
}

.iziModal.hasShadow:after {
    height: 30px;
    opacity: 1
}

.iziModal .iziModal-progressbar {
    position: absolute;
